.box-main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 120px;
  height: 180px;
  margin-bottom: 70px;
}

.box-image {
  max-height: 100%;
  max-width: 100%;
}

.box-title {
  font-family: 'Open Sans', 'Roboto', sans-serif;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.box-title > p {
  font-size: 11px;
}

@media only screen
  and (min-width: 320px)
  and (max-width: 667px) {
  .box-main {
    width: 200px;
    height: 250px;
    margin-bottom: 200px;
  }

  .box-title {
    -ms-flex-item-align: center;
        align-self: center;
  }

  .box-title > p {
    font-size: 16px;
  }
}



