.header-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 90px;
  color: white;
  font-size: 12px;
}

.header-nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 100px;
  padding-right: 100px;
  height: 55%;
  background-image: -webkit-gradient(linear,left top, left bottom,from(#1DA4FC),to(#0072FF));
  background-image: -webkit-linear-gradient(#1DA4FC,#0072FF);
  background-image: -o-linear-gradient(#1DA4FC,#0072FF);
  background-image: linear-gradient(#1DA4FC,#0072FF);
}

.header-title {
 font-size: 27px;
 font-weight:400;
}

.login-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 27px;
}

.login-container > div:first-child {
  background: transparent;
  margin-right: 12px;
}

.login-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-right: 10px;
  padding-left: 10px;
  background-color: #414141;
}

.entries-type {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 45%;
  padding-left: 100px;
  background-color: #414141;
  -webkit-box-shadow:0 5px 10px rgba(0, 0, 0, 0.4);
          box-shadow:0 5px 10px rgba(0, 0, 0, 0.4);
}

.entries-type-title > p {
 font-size: 17px;
}

@media only screen
  and (min-width: 320px)
  and (max-width: 667px) {
    .header-container {
      height: 140px;
      font-size: 7px;
    }

    .header-nav {
      -ms-flex-direction: column;
          flex-direction: column;
      padding: 0 0 10px;
    }

    .header-title {
     font-size: 22px;
    }

    .entries-type {
      -ms-flex-pack: center;
          justify-content: center;
      height: 35%;
      padding-left: 0;
    }
}



